.section--footer {
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  .social-media-links-list {
    display: flex;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0 auto;

    .item {
      margin: 0 10px;

      a {
        display: block;
        height: 20px;
        width: 20px;
      }
    }

    svg {
      fill: #FD5B45;
    }
  }
}
