.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;

  .social-media-links-list {
    display: flex;
    align-items: center;
    list-style: none;
    padding: 0;
    margin: 0;

    .item {
      height: 20px;
      width: 20px;
      margin-left: 10px;
    }

    svg {
      fill: #FD5B45;
    }
  }
}
