// mixins
@mixin media-query($size) {
	@if $size == xsmall {
		@media (max-width: 767px) { @content; }
	}
	@else if $size == small {
		@media (min-width: 768px) { @content; }
	}
	@else if $size == medium {
		@media (min-width: 992px) { @content; }
	}
	@else if $size == large {
		@media (min-width: 1200px) { @content; }
	}
}

@mixin transition($prop, $seconds) {
	-webkit-transition: $prop $seconds ease-in;
	-moz-transition: $prop $seconds ease-in;
	-ms-transition: $prop $seconds ease-in;
	-o-transition: $prop $seconds ease-in;
	transition: $prop $seconds ease-in;
}