body {
  background-color: $body-bg;
}

a {
  color: $highlight;
}

p {
  margin: 0 0 10px;
}

.section {
  padding: 10px;

  &--about {
    height: 40vh;
    text-align: center;
  }
}

@include media-query(small) {
  .content {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .section {
    padding: 20px;

    &--about {
      height: auto;
      text-align: right;
      width: 50%;
    }
    &--face {
      width: 50%;
    }
  }
}