// colors
$body-bg: #ded6d2;
$white:#FFFFFF;
$black:#000000;
$highlight:#FD5B45;


// measurements
$width:300px;

// timing
$default:0.2s;

// mixins
@mixin media-query($size) {
	@if $size == xsmall {
		@media (max-width: 767px) { @content; }
	}
	@else if $size == small {
		@media (min-width: 768px) { @content; }
	}
	@else if $size == medium {
		@media (min-width: 992px) { @content; }
	}
	@else if $size == large {
		@media (min-width: 1200px) { @content; }
	}
}

@mixin font-size($sizeValue) {
	font-size: $sizeValue + px;
	font-size: ($sizeValue / 10) + rem;
}

@mixin aspect-ratio($width,$height) {
	position:relative;
	width:100%;

	&:before {
		content:'';
		display:block;
		float:left;
		height:100%;
		padding-top:$height/$width * 100%;
	}
}

@mixin transition($seconds) {
	-webkit-transition: all $seconds ease-in;
	-moz-transition: all $seconds ease-in;
	-ms-transition: all $seconds ease-in;
	-o-transition: all $seconds ease-in;
	transition: all $seconds ease-in;
}

@mixin transform($property) {
	-webkit-transform: $property;
	-moz-transform: $property;
	-ms-transform: $property;
	-o-transform: $property;
	transform: $property;
}

@mixin lighten-bg($color, $multiplier) {
    background-color:adjust-color($color,$lightness:$multiplier);
}
@mixin lighten-text($color, $multiplier) {
	color:adjust-color($color,$lightness:$multiplier);
}
@mixin background-gradient($colorA, $colorB) {
	background: $colorA;
	background: -moz-linear-gradient(left, $colorA 0%, $colorB 100%);
	background: -webkit-gradient(linear, left top, right top, color-stop(0%,$colorA), color-stop(100%,$colorB));
	background: -webkit-linear-gradient(left, $colorA 0%,$colorB 100%);
	background: -o-linear-gradient(left, $colorA 0%,$colorB 100%);
	background: -ms-linear-gradient(left, $colorA 0%,$colorB 100%);
	background: linear-gradient(to right, $colorA 0%,$colorB 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$colorA', endColorstr='$colorB',GradientType=1 );
}